	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/bbc-releases-an-air-version-of-the-iplayer/index.md absPath of file >>> MarkdownRemark","html":"<p>You can install it from here, when you first go to the page click \"I want to be a labs tester\", then visit the link again and you'll be able to download it.</p>\n<p><a href=\"http://www.bbc.co.uk/iplayer/install/bbc_iplayer_desktop\">http://www.bbc.co.uk/iplayer/install/bbc_iplayer_desktop</a></p>\n<p>One of it's coolest features I've seen so far (I've used it for 3mins!) is that you browse the videos online like normal, but then instead of playing the video you click the download button which talks to the AIR app and starts downloading the video in the AIR application. You can then watch the video for up to 30 days after you've downloaded it online or offline. They've used a Flash Media Rights Management Server to DRM-Protect content so you shouldn't be able to get to the video file. Pretty cool stuff.</p>\n<p>You can find more info here: <a href=\"http://www.ashorten.com/2008/12/19/bbc-launches-iplayer-desktop-on-adobe-air/\">http://www.ashorten.com/2008/12/19/bbc-launches-iplayer-desktop-on-adobe-air/</a></p>","frontmatter":{"title":"BBC Releases an AIR version of the iPlayer","date":"December 22, 2008","tags":["air-applications","bbc"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/e-newsletter-the-benefits-of-using-adobe-air/","tags":["air","e-newsletter"],"title":"E-Newsletter / The benefits of using Adobe AIR.","date":"2008-12-19T21:34:22.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/imagesizer-updated-to-version-0310/","tags":["imagesizer"],"title":"ImageSizer - updated to version 0.3.10!","date":"2008-12-22T14:11:45.000Z","description":null,"image":null}}}}

